/* Owner Section */
.owner-section {
  display: flex;
  justify-content: center;
  padding: 50px 0;
  background-color: #f8f8f8;
}

.owner-card {
  display: flex;
  align-items: center;
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 900px; /* Increase max width */
  width: 100%; /* Ensure it takes full width within max-width */
  margin: 0 20px; /* Space on both sides of the card */
}

.owner-image {
  width: 200px; /* Increased size of the image */
  height: 200px; /* Make height equal to width for better balance */
  margin-right: 30px; /* More space between the image and text */
  object-fit: cover; /* Ensures the image maintains aspect ratio */
  border-radius: 10px; /* Slight rounded corners for the image */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.owner-image:hover {
  transform: scale(1.05); /* Slight zoom effect */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15); /* Soft shadow on hover */
}

.owner-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 600px; /* Adjust to fit text */
}

.owner-text h2 {
  font-size: 32px; /* Larger font for better emphasis */
  margin-bottom: 10px;
  color: #222; /* Darker shade for better contrast */
  font-weight: 700; /* Bolder text */
}

.owner-text p {
  font-size: 18px; /* Slightly bigger font for better readability */
  color: #444; /* Darker text for better contrast */
  line-height: 1.6;
  margin-bottom: 20px;
}

.owner-name {
  font-size: 20px;
  font-weight: 600;
  color: #222; /* Owner's name with bold contrast */
  margin-top: 10px;
}